
.index {
  width: 100%;
  position: relative;
  height: calc(100vh - 80px);
  @include phone {
    // +ratio(750,1000)
    height: auto; }


  .back-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    .video-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 0%; }

    .moblie-index {
      display: none;
      img {
        display: block;
        width: 100%; } }
    @include phone {
      position: relative;
      .video-bg {
        display: none; }
      .moblie-index {
        @include phone {
          display: block; } } } }

  .front-wrap {
    @include full;

    .textLeft {
      position: absolute;
      top: 35px;
      left: 70px;
      // transform: translate(0, -50%)
      @media screen and (max-height: 900px) {
        transform: translate(0%, 0%) scale(.8);
        transform-origin: 0% 0%; }
      @media screen and (max-height: 780px) {
        transform: translate(0%, 0%) scale(.6);
        transform-origin: 0% 0%; }
      @include phone {
        // left: 10px
        // top: 30px
        // transform: translate(0%, 0%) scale(.7)
        // transform-origin: 0% 0%
        display: none; }

      .txt-content {
        @include aniPicsWrap; }
      .t1 {
        @include bg('index-t1.png'); }

      .t2 {
        @include bg('index-t2.png'); } }


    .textRight {
      position: absolute;
      right: 25px;
      bottom: 220px;
      .txt-content {
        @include aniPicsWrap; }

      @media screen and (max-height: 900px) {
        transform: translate(0%, 0%) scale(.8);
        transform-origin: 100% 100%;
        right: 15px;
        bottom: 180px; }
      @media screen and (max-height: 780px) {
        transform: translate(0%, 0%) scale(.6); }
      @media screen and (max-height: 600px) {
        transform: translate(0%, 0%) scale(.5); }
      @media screen and (max-height: 555px) {
        transform: translate(0%, 0%) scale(.4);
        right: 30px; }
      @include phone {
        // right: 9px
        // top: auto
        // bottom: 120px
        // transform: translate(0%, 0%) scale(.7)
        // transform-origin: 100% 100%
        display: none; }
      .t1 {
        @include bg('index-tt1.png'); }

      .t2 {
        @include bg('index-tt2.gif'); }

      .t3 {
        @include bg('index-tt3.png'); }

      .t4 {
        @include bg('index-tt4.png'); } } } }






